.benefits {
    background-color: #f9f9f9;

    &__list {
        display: grid;
        grid-template-columns: repeat(12, 1fr);
        gap: 40px;

        @include mobile {
            grid-template-columns: repeat(4, 1fr);
        }
    }

    &__item {
        grid-column: 4 span;

        @include high-desktop {
            grid-column: 6 span;
        }
    }

    &__subtitle {
        display: inline-flex;
        align-items: center;
        font-weight: 600;
        font-size: 24px;
        line-height: 160%;
        color: var(--dark-color);
        margin-bottom: 20px;
    }

    &__descr {
        font-weight: 400;
        font-size: 18px;
        line-height: 160%;
        color: var(--text-color);
    }
}